@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&family=Roboto:wght@300;400&display=swap);
*{box-sizing:border-box}body{padding:0;margin:0 auto;height:100vh;color:white;background-color:#FF5656;font-family:'Nunito', sans-serif;font-weight:700;font-size:18px;display:block}.App{display:flex;flex-direction:column;align-items:center;justify-content:center}.Header{display:flex;justify-content:center;align-items:center;flex-direction:column}.Header img{object-fit:contain;height:240px}.Header h2{width:500px;text-align:center;font-size:1.5rem;font-weight:300;letter-spacing:3px;z-index:2;margin-top:0;font-family:'Roboto', sans-serif}.Timer{display:grid;grid-template-columns:1fr 1fr;grid-template-rows:2fr 1fr;text-align:center;background:#fc7171;border-radius:5px;padding:0.5rem}.SessionLength{background:#dd5252;margin:0.5rem;padding:1rem;border-radius:5px;grid-column:span 2;display:flex;flex-direction:column;align-items:center;justify-content:center;position:relative}.SessionLength__label{font-size:2rem;margin-top:0;margin-bottom:0.5rem}.SessionLength__time{font-size:5rem;margin-bottom:1rem}.SessionLength__controls{position:absolute;bottom:0}.SessionLength__controls__btn{height:30px;width:100%;background:#fff;border:none;border-radius:5px;cursor:pointer;margin:0 1rem;font-size:1.5rem;font-weight:700;height:35px;width:85px;border-bottom-left-radius:0;border-bottom-right-radius:0}.SessionLength__controls__btn:active{box-shadow:inset 0 0 0 1px rgba(0,0,0,0.15),inset 0 0 6px rgba(0,0,0,0.2)}.SessionLength__controls__btn--reset{width:50px;font-size:1.4rem}.TimerLengthControl{background:#dd5252;margin:0.5rem;padding:1rem;border-radius:5px}.TimerLengthControl__title{margin:0 auto;margin-bottom:0.5rem;font-size:1.5rem}.TimerLengthControl__controls{display:flex;justify-content:center;align-items:center}.TimerLengthControl__controls__btn{height:30px;width:100%;background:#fff;border:none;border-radius:5px;cursor:pointer;height:35px}.TimerLengthControl__controls__btn:active{box-shadow:inset 0 0 0 1px rgba(0,0,0,0.15),inset 0 0 6px rgba(0,0,0,0.2)}.TimerLengthControl__controls__btn--left{border-top-right-radius:0;border-bottom-right-radius:0}.TimerLengthControl__controls__btn--right{border-top-left-radius:0;border-bottom-left-radius:0}.TimerLengthControl__controls__length{display:flex;align-items:center;justify-content:center;color:#000;background:#fff;width:4rem;height:35px;font-size:1.5rem;margin:0;padding:0.5rem;border-right:1px solid #d3d3d3;border-left:1px solid #d3d3d3}.TimerLengthControl__controls__length span{vertical-align:middle}.footer{font-family:'Roboto', sans-serif;font-weight:300;letter-spacing:1.8px;font-size:1rem}.footer a{text-decoration:none;color:black}@media only screen and (max-width: 550px){.Header img{height:230px}.Header h2{font-size:1.5rem;width:380px}.SessionLength__label{font-size:1.7rem}.SessionLength__time{font-size:4rem}.TimerLengthControl__title{font-size:1.2rem}.TimerLengthControl__controls__length{font-size:1.4rem}i{font-size:1rem}}@media only screen and (max-width: 400px){.Timer{padding:0.4rem}.Header img{height:150px}.Header h2{font-size:1.1rem;width:300px}.SessionLength{padding:0.4rem;margin:0.4rem}.SessionLength__label{font-size:1.5rem}.SessionLength__time{font-size:3rem}.TimerLengthControl{padding:0.4rem;margin:0.4rem}.TimerLengthControl__title{font-size:1rem}.TimerLengthControl__controls__length{font-size:1.1rem}i{font-size:1rem}}

